body {
  margin: 0; /* Supprime la marge par défaut du body */
  padding: 0; /* Supprime le padding par défaut du body */
  height: 100vh;
  background-image: linear-gradient(
    118deg,
    #a0a2a4 10%,
    rgb(43, 42, 42) 77.86%
  );
}

.image-home {
  display: flex; /* Ajoutez Flexbox pour centrer l'image */
  justify-content: center;
  align-items: center;
  width: 60%;
  position: absolute;
  top: 10vh;
  right: 0;
  pointer-events: none;
}

.image-home img {
  width: 100%;
}

@media (max-width: 1400px) {
  .welcome-text-h1 {
    font-size: 6.5vw; /* Taille de la police lorsque le texte dépasse la div parente */
  }
  .welcome-text-h2 {
    font-size: 2.5vw;
  }
}

@media (max-width: 1100px) {
  .welcome-text {
  }
  .welcome-text-h1 {
    font-size: 6vw;
  }
  .welcome-text-h2 {
    font-size: 2vw;
  }
  .logoLink-container {
    bottom: 10vh;
    /* top: 60vh; */
  }
  /* Les liens github, insta... */
  .logoLink {
    width: 8vw;
  }
}

@media screen and (max-aspect-ratio: 1/1) and (max-aspect-ratio: 8/5) and (max-width: 850px) {
  /* Styles spécifiques pour les écrans de 1200 pixels ou plus */
  .welcome-text {
    justify-content: center; /* Centrer horizontalement */
    align-items: center; /* Centrer verticalement */
    top: 10vh;
    width: 100%;
    height: 20vh; /* Pour couvrir toute la hauteur de la fenêtre */
    text-align: center; /* Pour centrer le texte horizontalement */
  }

  .welcome-text-h1 {
    /* width: 0%; */
    font-family: "Playfair Display";
    font-size: 14vw;
    height: 2vh;
    font-style: normal;
    line-height: normal;
  }

  .welcome-text-h2 {
    font-size: 3vw;
  }

  .logo {
    width: 10vh;
    margin-left: 5%;
  }

  .logoLink-container {
    width: 100%;
    /* bottom: 10vh; */
    top: 35vh;
    /* justify-content: center; */
    text-align: center; /* Pour centrer le texte horizontalement */

    /* height: 10%; */
  }

  /* Les liens github, insta... */
  .logoLink {
    width: 20vw;
  }

  .image-home {
    /* position: relative; */
    justify-content: center;
    align-items: center; /* Centrer verticalement */
    text-align: center;
    width: 100%;
    left: 0;
    top: 55vh;
  }
}
