.welcome-text {
  position: absolute;
  /* width: 34%; */
  width: 40%;
  top: 25vh;
  left: 4vw;
  -webkit-user-select: none; /* Pour les navigateurs basés sur WebKit (Chrome, Safari) */
  -moz-user-select: none; /* Pour les navigateurs basés sur Gecko (Firefox) */
  -ms-user-select: none; /* Pour Internet Explorer */
  user-select: none; /* Norme */
}

.welcome-text-h1 {
  color: #000;
  font-family: "Playfair Display";
  /* font-size: 6vw; Utilisez une taille de police responsive basée sur la largeur de la vue (viewport) */
  font-size: 96px; /* Utilisez une taille de police responsive basée sur la largeur de la vue (viewport) */
  font-style: normal;
  font-weight: 700;
  line-height: normal; /* Ajustez la hauteur de ligne selon vos préférences */
  width: 100%;
  height: 6vh;
  white-space: nowrap;
}

.welcome-text-h2 {
  color: #000;
  font-family: "Source Sans Pro";
  font-size: 36px; /* Utilisez une taille de police responsive basée sur la largeur de la vue (viewport) */
  font-style: normal;
  font-weight: 700;
  line-height: 1.5; /* Ajustez la hauteur de ligne selon vos préférences */
}
