li {
  margin-right: 10%;

  @media screen and (max-aspect-ratio: 1/1) and (max-aspect-ratio: 8/5) and (max-width: 850px) {
    display: none;
  }
}

a {
  text-decoration: none;
  text-align: center;
  font-size: 1.5vw;
  font-family: Playfair Display;
  font-weight: 700;

  -webkit-user-select: none; /* Pour les navigateurs basés sur WebKit (Chrome, Safari) */
  -moz-user-select: none; /* Pour les navigateurs basés sur Gecko (Firefox) */
  -ms-user-select: none; /* Pour Internet Explorer */
  -user-select: none; /* Norme */

  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  -ms-user-drag: none;
  -user-drag: none;
}

.navSectionElement {
  color: #ffffff;
}

.navSectionElement.clicked {
  color: #91bed3;
}

.navSectionElement:hover {
  color: #91bed3;
}

.navSectionElement {
  color: #fff;
  /* text-transform: uppercase; */
  text-decoration: none;
  /* letter-spacing: 0.15em; */

  display: inline-block;
  /* padding: 15px 20px; */
  position: relative;
}
.navSectionElement:after {
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 2px;
  background: #91bed3;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}
.navSectionElement.clicked:after {
  width: 100%;
  left: 0;
}
